import React, { ReactElement } from 'react';
import { server, POST } from '../utils/requests';
import url from 'url';
import styles from '../css/tournament.module.css';
import { TournamentStatus, TournamentSettings } from '../interfaces';
import { EditableField } from '../components/';
import { MdExitToApp, MdStop } from 'react-icons/md';
import { IoMdPlay, IoMdGrid } from 'react-icons/io';

interface InfoProps {
	status: TournamentStatus
	settings: TournamentSettings
	updateTournamentStatus: () => void
	round: number
	totalRounds: number | 'Infinity'
	closeTournament: () => void
	toggleCrossTable: () => void

	lastRoundOverride: boolean
	setLastRound: (override: boolean) => void
	triggerError: (message: string) => void
}

export default function InfoProps(props: InfoProps): ReactElement {

    let lastRound = false;
    if (typeof props.lastRoundOverride !== 'undefined') lastRound = props.lastRoundOverride;
    else lastRound = props.settings.totalRounds === props.status.round;

    let uri = url.resolve(server, ['tournament', props.status.id, 'setName'].join('/'));

    return (
        <div className={styles.titleBar} style={{
            gridColumn: '2 /span 1'
        }}>
            <div className={styles.row}>
                <div className={['topBanner', styles.header].join(' ')}>
                    <EditableField
                        content={props.status.name}
                        url={uri}
                        inputType='text'
                        handleSubmit={(k: string, v: any) => POST({
                            url: uri,
                            data: {
                                [k]: v
                            }
                        })}
                        additionalStyles={styles}
                        additionalInputProps={{}}
                        onSubmitSuccess={(e) => {
                            props.updateTournamentStatus();
                            return e;
                        }}
                        defaultContent='Unnamed Swiss Tournament'
                        show={{
                            submit: true
                        }}
                    />
                </div>
            </div>
            <div className={styles.row}>
                <div className={'topBanner ' + styles.header}>
                    ID: {props.status.id}
                </div>
                <div className={'topBanner ' + styles.header}>
                    Round: {!props.status.round ? 'Not yet initialised' : props.status.round + ' / ' + (props.totalRounds === 'Infinity' ? '∞' : props.totalRounds)}
                </div>
            </div>
            <div className={styles.row}>
                <div className='button' onClick={props.closeTournament}>
                    <span role='img' aria-label='back'>
                        <MdExitToApp />
                    </span>
                </div>
                <div className='button' onClick={() => {
                    if (props.status.round >= props.settings.totalRounds) props.triggerError('Increase the number of rounds in Settings to continue!')
                    props.setLastRound(!lastRound);
                }}>
                    <span role='img' aria-label='decommission'>
                        {lastRound ? 
                            <IoMdPlay /> :
                            <MdStop />
                        }
                    </span>
                </div>
                <div className='button' onClick={props.toggleCrossTable}>
                    <IoMdGrid />
                </div>
            </div>
        </div>
    );

}